import React, {
  useState,
  useEffect,
  useRef,
  useCallback,
  useContext
} from "react";
import { Table, message } from "antd";
import axios from "../../network";
import classnames from "classnames";
import { parseTime, mergeSingers, parseCopyright } from "../../utils";
import {
  HeartOutlined,
  DownloadOutlined,
  SoundFilled
} from "@ant-design/icons";
import { PaginationProps } from "antd/es/pagination";
import { connect } from "react-redux";
import { UnionStateTypes } from "../../store";
import {
  getSetPlayDetailMapAction,
  getPushPlayQueueAction,
  fetchPlayUrl,
  getChangePlayIndexAction,
  getChangePlayStateAction
} from "../../common/Player/store";
import ResultOfPagination from "./resultOfPagination";
import { useHistory } from "react-router-dom";
import { SetHistoryStackContext } from "../../pages/Home";

export interface IQueryParams {
  page: number;
  limit: number;
}

const ResultOfSongs: React.FC<ResultOfSongsProps> = (props) => {
  const {
    keywords,
    setLoading,
    setSearchCount,
    current,
    playState,
    setPlayDetailMap,
    pushPlayQueue,
    fetchPlayUrl,
    changePlayIndex,
    changePlayState,
    playQueue,
    urlMap
  } = props;
  const [songs, setSongs] = useState<ISearchOfSongItem[]>([]);
  const [show, setShow] = useState(false);
  const classes = classnames("result-of-songs", {
    hidden: !show
  });
  const [pagination, setPagination] = useState<PaginationProps>({
    current: 1
  });
  const map = useRef<Map<string, PlayDetailItem>>(
    new Map<string, PlayDetailItem>()
  );
  const [queryParams, setQueryParams] = useState<IQueryParams>({
    page: 0,
    limit: 100
  });
  const context = useContext(SetHistoryStackContext);
  const history = useHistory();

  const handlePageSizeChange = useCallback((page: number) => {
    setQueryParams((data) => {
      return {
        page: page - 1,
        limit: data.limit
      };
    });
  }, []);

  function renderIndex(value: any, record: ISearchOfSongItem) {
    return (
      <div className="index">
        {!record.hasCopyRight ? (
          ""
        ) : record.id === current ? (
          <SoundFilled className={`${playState ? "play" : ""}`} />
        ) : (
          <span>{value}</span>
        )}
      </div>
    );
  }

  function handleDoubleClick(record: ISearchOfSongItem) {
    if (!record.hasCopyRight) {
      return message.error({
        content: ">_<"
      });
    }
    if (current === record.id && playState === false) {
      return changePlayState(true);
    } else if (current === record.id) return;
    if (playQueue.indexOf(record.id) !== -1) {
      if (!urlMap.get(record.id)) {
        fetchPlayUrl(record.id);
      }
      changePlayIndex(record.id);
    } else {
      pushPlayQueue(record.id);
      fetchPlayUrl(record.id);
      changePlayIndex(record.id);
    }
  }

  useEffect(() => {
    function returnUrl() {
      const { page, limit } = queryParams;
      const offset = page * limit;
      return `/cloudsearch?keywords=${keywords}&offset=${offset}&limit=${limit}`;
    }
    function parseData(songs: any[]) {
      const _arr: ISearchOfSongItem[] = [];
      if (!(songs instanceof Array)) return [];
      for (let i = 0; i < songs.length; i++) {
        const {
          al: { name, picUrl },
          ar,
          id,
          dt,
          noCopyrightRcmd,
          alia,
          pop,
          fee
        } = songs[i];
        const copyright = parseCopyright(noCopyrightRcmd, fee);
        const parsedItem = {
          duration: parseTime(dt),
          singers: mergeSingers(ar),
          id,
          index: (i + 1).toString().padStart(2, "0"),
          album: name,
          picUrl,
          name: songs[i].name,
          hasCopyRight: copyright,
          alia: alia.join(","),
          albumId: songs[i].al.id
        };
        if (copyright) {
          map.current.set(id, parsedItem);
        }
        _arr.push(Object.assign({}, parsedItem, { pop }));
      }

      return _arr;
    }
    async function fetchData() {
      map.current = new Map<string, PlayDetailItem>();
      setSongs([]);
      setShow(false);
      setLoading(true);
      axios.get(returnUrl()).then(({ data }) => {
        setPagination({
          total: data.result.songCount
        });
        setSearchCount(data.result.songCount);
        setSongs(parseData(data.result.songs));
        setPlayDetailMap(map.current);
        setLoading(false);
        setShow(true);
      });
    }

    if (keywords) {
      fetchData();
    }
  }, [keywords, queryParams, setPlayDetailMap, setLoading, setSearchCount]);

  function renderPop(value: any) {
    return (
      <div className="pop">
        <div className="pop-inner" style={{ width: value + "%" }}></div>
      </div>
    );
  }

  function renderManipulation() {
    return (
      <div className="steer">
        <HeartOutlined />
        <DownloadOutlined />
      </div>
    );
  }

  function toSingerDetail(id: string) {
    context.setHistoryStack("push", "singerDetail");
    history.push("/singerDetail/" + id);
  }

  function toAlbum(id: string) {
    context.setHistoryStack("push", "album");
    history.push("/album/" + id);
  }

  return (
    <div className={classes}>
      <Table
        size="small"
        bordered
        dataSource={songs}
        rowKey={(record) => record.id}
        pagination={false}
        onRow={(record) => {
          return {
            onDoubleClick: () => handleDoubleClick(record)
          };
        }}
      >
        <Table.Column
          title=""
          width={65}
          dataIndex="index"
          render={renderIndex}
        />
        <Table.Column title="" width={65} render={renderManipulation} />
        <Table.Column title="" ellipsis dataIndex="name" />
        <Table.Column
          title=""
          ellipsis
          dataIndex="singers"
          render={(value: ISingerInfo[]) => {
            return value.map((item) => (
              <span
                key={item.id}
                className="singer-name"
                onClick={() => toSingerDetail(item.id)}
              >
                {item.name}
              </span>
            ));
          }}
        />
        <Table.Column
          title=""
          ellipsis
          dataIndex="album"
          render={(value: any, record: ISongSheetItem) => {
            return (
              <span
                onClick={() => toAlbum(record.albumId)}
                className="album-name"
              >
                {record.album}
              </span>
            );
          }}
        />
        <Table.Column title="" width={100} dataIndex="duration" />
        <Table.Column
          title=""
          width={120}
          dataIndex="pop"
          render={renderPop}
        />
      </Table>
      <ResultOfPagination
        attrs={pagination}
        handlePageSizeChange={handlePageSizeChange}
        defaultPageSize={100}
      />
    </div>
  );
};

const mapStateToProps = (state: UnionStateTypes) => {
  const player = state.player;
  const current = player.current;

  return {
    current: player.playQueue[current],
    playState: player.playState,
    playQueue: player.playQueue,
    urlMap: player.playUrlMap
  };
};

const mapDispatchToProps = (dispatch: any) => {
  return {
    setPlayDetailMap(map: Map<string, PlayDetailItem>) {
      dispatch(getSetPlayDetailMapAction(map));
    },
    pushPlayQueue(ids: string) {
      dispatch(getPushPlayQueueAction(ids, false));
    },
    fetchPlayUrl(id: string) {
      dispatch(fetchPlayUrl(id));
    },
    changePlayIndex(id: string) {
      dispatch(getChangePlayIndexAction(id));
    },
    changePlayState(flag: boolean) {
      dispatch(getChangePlayStateAction(flag));
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(React.memo(ResultOfSongs));
