import React from "react";
import { DownOutlined } from "@ant-design/icons";
import CategoryItem from "./categoryItem";

const iconclass = [
  "icon-diqiu",
  "icon-fengge",
  "icon-kafei",
  "icon-xiaolian",
  "icon-zhuti_"
];

const AllCategories: React.FC<AllCategoriesProps> = (props) => {
  const { categories, currCategory, changeCategory, show, changeShow } = props;

  function extendDropdown() {
    changeShow(!show);
  }

  function handleClick(category: string) {
    extendDropdown();
    changeCategory(category);
  }

  return (
    <div className="all-categories">
      <div className="all-categories-btn" onClick={extendDropdown}>
        {currCategory}
        <DownOutlined />
      </div>
      <div className={"all-categories-dropdown " + (show ? "show" : "")}>
        <div className="all-categories-dropdown-title"></div>
        <div className="all-categories-dropdown-content">
          <div>
            <div
              className={
                "all-categories-dropdown-content-top " +
                (currCategory === "" ? "selected" : "")
              }
              onClick={() => handleClick("")}
            >
              
            </div>
            {categories.map((item, index) => {
              return (
                <CategoryItem
                  {...item}
                  icon={iconclass[index]}
                  key={item.categoryName}
                  onClick={handleClick}
                  currCategory={currCategory}
                />
              );
            })}
          </div>
        </div>
      </div>
    </div>
  );
};

export default React.memo(AllCategories);
