import React, { useEffect, useRef } from "react";

interface LazyloadProps {
  src: string;
  className?: string;
}

const Lazyload: React.FC<LazyloadProps> = (props) => {
  const { src, className } = props;
  const imgRef = useRef<HTMLImageElement>(null);
  const handleIntersection: IntersectionObserverCallback = (entries) => {
    entries.forEach((item) => {
      // entries
      if (item.isIntersecting) {
        // 
        const target = item.target as HTMLImageElement;
        target.src = src;
        io.current.unobserve(item.target); //  callback
      }
    });
  };
  const io = useRef(new IntersectionObserver(handleIntersection));

  useEffect(() => {
    const image = new Image();
    image.src = src;
  }, [src]);

  useEffect(() => {
    const _io = io.current;
    if (imgRef && imgRef.current && io && io.current) {
      io.current.observe(imgRef.current);
    }
    return () => {
      _io.disconnect();
    };
  }, [imgRef]);

  return (
    <img
      src="data:image/png;base64,"
      alt=""
      className={"lazyload " + className}
      ref={imgRef}
    />
  );
};

export default React.memo(Lazyload);
