import React, { useEffect, useState, useRef, useContext } from "react";
import {
  FireFilled,
  PlayCircleOutlined,
  FolderAddOutlined,
  ShareAltOutlined,
  DownloadOutlined,
  UpOutlined,
  DownOutlined
} from "@ant-design/icons";
import { parseDate, parsePlayCount } from "../../utils";
import PlaylistInfo from "../../common/PlaylistInfo";
import axios from "../../network";
import { connect } from "react-redux";
import { getPushPlayQueueAction } from "../../common/Player/store";
import { message } from "antd";
import { match, useHistory } from "react-router-dom";
import { SetHistoryStackContext } from "../Home";

interface PlaylistProps {
  pushPlayQueue: (ids: string | Array<string>) => void;
  match: match;
}

interface IDetail {
  coverImgUrl: string;
  // 
  name: string;
  tags?: Array<string>;
  avatarUrl: string;
  description?: string;
  playCount: number;
  subscribedCount: number;
  shareCount: number;
  createTime: number;
  commentCount: number;
  // 
  nickname: string;
  // 
  trackCount: number;
}

const PlayList: React.FC<PlaylistProps> = (props) => {
  const [collapse, setCollapse] = useState(true);
  // id
  const [info, setInfo] = useState<PlaylistInfoProps>();
  // 
  const [detail, setDetail] = useState<IDetail>();
  const isAdded = useRef(false);
  const { pushPlayQueue } = props;
  const match = props.match as match<{ id: string }>;
  const history = useHistory();
  const context = useContext(SetHistoryStackContext);

  useEffect(() => {
    function parseSubscriber(subscribers: Array<any>): Array<ISubscriber> {
      const _subscribers = [];
      for (let i = 0; i < subscribers.length; i++) {
        const subscriber: ISubscriber = {
          avatarUrl: subscribers[i].avatarUrl,
          userId: subscribers[i].userId
        };
        _subscribers.push(subscriber);
      }
      return _subscribers;
    }

    function parseInfo(playlist: any): PlaylistInfoProps {
      return {
        trackIds: playlist.trackIds.map((track: any) => track.id),
        subscribers: parseSubscriber(playlist.subscribers),
        playlistId: playlist.id
      };
    }

    function parseDetail(playlist: any): IDetail {
      return {
        coverImgUrl: playlist.coverImgUrl,
        name: playlist.name,
        tags: playlist.tags,
        avatarUrl: playlist.creator.avatarUrl,
        description: playlist.description,
        playCount: playlist.playCount,
        subscribedCount: playlist.subscribedCount,
        shareCount: playlist.shareCount,
        createTime: playlist.createTime,
        commentCount: playlist.commentCount,
        nickname: playlist.creator.nickname,
        trackCount: playlist.trackCount
      };
    }

    async function fetchPlaylistInfo(id: string) {
      const { data } = await axios.get("/playlist/detail?id=" + id);
      setDetail(parseDetail(data.playlist));
      setInfo(parseInfo(data.playlist));
    }

    if (match.params.id) {
      setInfo(undefined);
      fetchPlaylistInfo(match.params.id);
    }
  }, [match.params.id]);

  function pushPlayQueueAll() {
    if (isAdded.current) {
      return message.warn({
        content: ""
      });
    }
    if (info) {
      pushPlayQueue(info.trackIds);
      isAdded.current = true;
    }
  }

  function toTotalPlaylist(type: string) {
    context.setHistoryStack("push", "totalPlaylist");
    history.push("/totalPlaylist/" + type);
  }

  return (
    <>
      <div className="playlist">
        <div>
          <div className="playlist-top">
            <div className="cover-image ">
              {detail && <img src={detail.coverImgUrl} alt="" />}
            </div>
            {detail && detail.description && (
              <div className="collapse">
                {collapse ? (
                  <DownOutlined onClick={() => setCollapse(false)} />
                ) : (
                  <UpOutlined onClick={() => setCollapse(true)} />
                )}
              </div>
            )}
            <div className="playlist-detail">
              <div className="playlist-detail-panel-1">
                <div style={{ float: "left" }}>
                  <span className="tag"></span>
                  <span className="content">{detail?.name}</span>
                </div>
              </div>
              <div className="playlist-detail-panel-2">
                <img src={detail?.avatarUrl} alt="" />
                <span className="nickname">{detail?.nickname}</span>
                <span className="createTime">
                  {detail?.createTime &&
                    parseDate("YYYY-mm-dd", detail.createTime)}
                  
                </span>
              </div>
              <div className="playlist-detail-panel-3">
                <div className="btn" onClick={pushPlayQueueAll}>
                  <PlayCircleOutlined />
                  
                </div>
                <div className="btn">
                  <FolderAddOutlined />
                  {`(${detail ? detail.subscribedCount : 0})`}
                </div>
                <div className="btn">
                  <ShareAltOutlined />
                  {`(${detail ? detail.shareCount : 0})`}
                </div>
                <div className="btn">
                  <DownloadOutlined />
                  
                </div>
              </div>
              {detail && detail.tags && (
                <div className="playlist-detail-panel-4">
                  :
                  {detail.tags.map((tag) => {
                    return (
                      <span key={tag} onClick={() => toTotalPlaylist(tag)}>
                        {tag}
                      </span>
                    );
                  })}
                </div>
              )}
              {detail && detail.description && (
                <div
                  className={`playlist-detail-panel-5 ${
                    collapse ? "collapsed" : ""
                  }`}
                >
                  :
                  {detail.description.trim()}
                </div>
              )}
            </div>
            <div className="right">
              <FireFilled style={{ marginRight: 10 }} />
              {detail?.trackCount}
              <PlayCircleOutlined style={{ margin: "0 10px" }} />
              {detail?.playCount && parsePlayCount(detail.playCount)}
            </div>
          </div>
          <div className="playlist-bottom">
            {info && <PlaylistInfo {...info} />}
          </div>
        </div>
      </div>
    </>
  );
};

const mapDispatchToProps = (dispatch: any) => {
  return {
    pushPlayQueue(ids: string | Array<string>) {
      dispatch(getPushPlayQueueAction(ids, false));
    }
  };
};

export default connect(null, mapDispatchToProps)(React.memo(PlayList));
